<template>
    <div>
        <!---->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item>GeoServer</el-breadcrumb-item>
            <el-breadcrumb-item>WFS</el-breadcrumb-item>
            <el-breadcrumb-item>WFS</el-breadcrumb-item>
        </el-breadcrumb>
        <!---->
        <el-card>
            <button id="insert" @click="insertClick"></button>
            <button id="update" @click="updateClick"></button>
            <button id="delete" @click="deleteClick"></button>
            <button id="end" @click="endClick"></button>
            <div id="map"></div>
        </el-card>
    </div>
</template>
<script>
import Map from 'ol/map';
import View from 'ol/view';
import Point from 'ol/geom/point';
import VectorLayer from 'ol/layer/vector';
import Vector from 'ol/source/vector';
import GeoJSON from 'ol/format/geojson';
import WFS from 'ol/format/wfs';
import Draw from 'ol/interaction/draw';
import Select from 'ol/interaction/select';
import Modify from 'ol/interaction/modify';
import Style from 'ol/style/style';
import Circle from 'ol/style/circle';
import Fill from 'ol/style/fill';
import loadingstrategy from 'ol/loadingstrategy';
import {getTdtLayer} from "../../../utils/searchUtils";
export default {
    data() {
        return {
            // 
            geoserverData: {
                wsName: 'webgistest',
                uri: 'http://www.openplans.org/webgistest',
                wfsURL: 'http://localhost:28081/geoserver/wfs?',
                layer: 'port'
            },
            select: null,
            edit: null,
            draw: null
        }
    },
    created() {
    },
    mounted() {
        this.initMap()
    },
    methods: {
        initMap() {
            const that = this
            // WFS  
            var wfsSource = new Vector({
                format: new GeoJSON(),
                url: function (extent) {
                    return (
                        that.geoserverData.wfsURL + 'service=WFS&version=1.1.0&request=GetFeature&typeName=' +
                        that.geoserverData.wsName + ":" +
                        that.geoserverData.layer + '&' + 'outputFormat=application/json&srsname=EPSG:4326&bbox=' +
                        extent.join(',') + ',EPSG:4326');
                },
                strategy: loadingstrategy.bbox
            });

            let wfsLayer = new VectorLayer({
                title: 'add Layer',
                source: wfsSource
            })

            var layers = [
              getTdtLayer("vec_w"),
              getTdtLayer("cva_w"), wfsLayer
            ];

            var map = new Map({
                target: 'map',
                layers: layers,
                view: new View({
                    projection: "EPSG:4326",
                    center: [114, 31],
                    zoom: 4
                })
            });

            map.on('pointermove', function (evt) {
                if (map.hasFeatureAtPixel(evt.pixel)) {
                    map.getTargetElement().style.cursor = "pointer"
                } else {
                    map.getTargetElement().style.cursor = "default"
                }
            });

            let select = new Select({
                layers: [wfsLayer],
                style: new Style({
                    image: new Circle({
                        radius: 8,
                        fill: new Fill({
                            color: "#f00"
                        })
                    })
                })
            });

            let edit = new Modify({
                features: select.getFeatures()
            });

            var draw = new Draw({
                source: new Vector({
                    features: []
                }),
                type: 'Point',
                freehand: false,//,
                stopClick: true
            });

            map.addInteraction(select);
            map.addInteraction(edit);
            map.addInteraction(draw);
            edit.setActive(false);
            draw.setActive(false);
            select.setActive(false);

            draw.on("drawend", function (e) {
                var feature = e.feature;
                that.sendWFS(feature, "insert");
            });
            select.on("select", function (e) {
                var features = e.selected;
                if (features.length > 0) {
                    let feature = features[0];
                    if (!edit.getActive()) {
                        that.sendWFS(feature, "delete");
                    }
                }
            });

            edit.on("modifyend", function (e) {
                // feature
                var features = e.features;
                var feature = features.item(0);
                that.sendWFS(feature, "update");
            });
            this.select = select
            this.edit = edit
            this.draw = draw
        },
        insertClick() {
            this.select.setActive(false)
            this.edit.setActive(false)
            this.draw.setActive(true);
        },
        updateClick() {
            this.draw.setActive(false)
            this.edit.setActive(true);
            this.select.setActive(true);
        },
        deleteClick() {
            this.draw.setActive(false)
            this.edit.setActive(false)
            this.select.setActive(true);
        },
        endClick() {
            this.draw.setActive(false)
            this.select.setActive(false)
            this.edit.setActive(false)
        },
        async sendWFS(feature, type) {
            //WFS  type insert update delect
            // 1Feature
            let ft = feature.clone()
            // id
            let id = feature.getId()
            let properties = ft.getProperties()
            var featObject;
            var WFSTSerializer = new WFS();
            var _this = this.geoserverData;
            let options = {
                featureNS: _this.uri,
                featurePrefix: _this.wsName, //
                featureType: _this.layer, //
                srsName: 'EPSG:4326'
            };
            if (type === "insert") {
                properties.address = ""
                properties.name = ""
                properties.porttype = "wnm"
                properties.province = "wnm"
                let coordinates = ft.getGeometry().getCoordinates()
                properties.lng = coordinates[0]
                properties.lat = coordinates[1]
                ft.setProperties(properties)

                let geom = ft.getGeometry()
                geom.applyTransform((flatCoordinates, flatCoordinates2, stride) => {
                    for (var j = 0; j < flatCoordinates.length; j += stride) {
                        var y = flatCoordinates[j]
                        var x = flatCoordinates[j + 1]
                        flatCoordinates[j] = x
                        flatCoordinates[j + 1] = y
                    }
                })
                ft.setGeometryName('geom') // postgisgeomshpthe_geom
                ft.setGeometry(geom)

                featObject = WFSTSerializer.writeTransaction([ft], null, null, options);
            } else if (type === "update") {
                ft.setId(id)
                properties.address = ""
                properties.name = ""

                let coordinates = ft.getGeometry().getCoordinates()
                properties.lng = coordinates[0]
                properties.lat = coordinates[1]
                ft.setProperties(properties)

                ft.getGeometry().applyTransform((flatCoordinates, flatCoordinates2, stride) => {
                    for (var j = 0; j < flatCoordinates.length; j += stride) {
                        var y = flatCoordinates[j]
                        var x = flatCoordinates[j + 1]
                        flatCoordinates[j] = x
                        flatCoordinates[j + 1] = y
                    }
                })
                ft.setGeometryName('geom')// postgisgeomshpthe_geom

                featObject = WFSTSerializer.writeTransaction(null, [ft], null, options);
            } else if (type === "delete") {
                ft.setId(id)
                featObject = WFSTSerializer.writeTransaction(null, null, [ft], options);
            }
            var serializer = new XMLSerializer();
            var featString = serializer.serializeToString(featObject);
            const { data: res } = await this.$http.post(this.geoserverData.wfsURL,
                featString,
                {
                    headers: {
                        //
                        "Content-Type": "application/xml"
                    }
                })
            if (res.indexOf("Exception") != -1) {
                console.log(res);
                alert("" + res)
            }
            else {
                alert("")
                window.location.reload();
            }
        }
    }
}
</script>

<style lang="less" scoped>

</style>
